(function ($) {
  var nextId = 0;
  var _elements = [];

  $.fn.overflowMenu = function (options) {

    return this.each(function () {
      var self = this;

      self.id = nextId++;
      options = options || {};

      var items = options.items || [];
      var target = null;

      self.menu = document.createElement('ul');
      self.menu.setAttribute('style', "position: fixed;");

      self.menu.className = 'bz-overflow-menu';
      self.menu.setAttribute('data-overflowmenu', self.id);

      // Create <li>'s for each menu item
      items.forEach(function (item, index) {
        var li = document.createElement('li');

        if (!('name' in item)) {
          // Insert a divider
          li.className = 'bz-overflow-menu__divider';
        } else {
          li.className = 'bz-overflow-menu__item bz-overflow-menu__item--comfortable';
          li.textContent = item.name;
          li.setAttribute('data-overflowmenuitem', index);
          li.setAttribute('tabindex', 0);
          li.addEventListener('touchend', select.bind(this, li));
        }
        self.menu.appendChild(li);
      });

      function show(evt) {
        target = evt.delegateTarget;

        var x = evt.originalEvent.clientX;
        var y = evt.originalEvent.clientY;
        var container = $(evt.delegateTarget).closest(".bz-section__content");
        if (container.length == 0) container = $("body");

        // Add root element to the <body>
        setTimeout(function () {
          container.append(self.menu);

          if (client.W() - x < self.menu.offsetWidth) {
            var xx = x - self.menu.offsetWidth;
          } else {
            var xx = x;
          }
          if (client.H() - y < self.menu.offsetHeight) {
            var yy = y - self.menu.offsetHeight;
          } else {
            var yy = y;
          }
          self.menu.style.left = xx + 'px';
          self.menu.style.top = yy + 'px';

          // Give overflow menu focus
          $(self.menu).focus();

          _elements.push(self.menu);
        }, 100);
      }

      // Hides overflow menu
      function hide() {
        $(self.menu).remove();
      }

      // Selects the given item and calls its handler
      function select(item, e) {
        var itemId = item.getAttribute('data-overflowmenuitem');
        if (items[itemId]) {
          // Call item handler with target element as parameter
          items[itemId].fn(target);
        }
        hide();
        e.preventDefault();
        e.stopPropagation();
      }

      // Listen click event to show menu
      $(self).bind('click', function (e) {
        var zIndex = getZIndex($(e.currentTarget));
        $(self.menu).css("z-index", zIndex);

        show(e);
      });
    });

  };

  var client = {
    W: function W() {
      return document.documentElement.clientWidth;
    },
    H: function H() {
      return document.documentElement.clientHeight;
    } };


  var removeMenu = function removeMenu() {

    if (_elements && _elements.length > 0) {
      _elements.forEach(function (el) {
        $(el).remove();
      });
    }

    _elements = [];
  };


  // Calculate the highest (top-most) zIndex in the document (minimum value returned: 2).
  var getZIndex = function getZIndex($context) {
    if ($context.get(0) === $('body').get(0)) return 2;

    var $parent = $($context).parent();
    var zIndex = parseFloat($parent.css('z-index'), -1);

    if (zIndex > -1) return zIndex;

    return getZIndex($parent);
  };

  document.addEventListener('touchstart', function (e) {
    if (e.target && !/bz-overflow-menu__item/i.test(e.target.className)) {
      removeMenu();
    }
  });

})(jQuery);