/*
*   Name: BizAgi Render Layout Label Class
*   Author: Ricardo Prez
*   Comments:
*   -   This script will define basic stuff for labels inside templates
*/

bizagi.rendering.tablet.layoutRender.extend("bizagi.rendering.tablet.layoutLabel", bizagi.rendering.layoutLabel.prototype);
// Extends itself
bizagi.rendering.tablet.layoutLabel.extend("bizagi.rendering.tablet.layoutLabel", {
  setValue: function setValue(val, triggerEvents) {
    var self = this;
    self.properties.displayName = val;
    this._super(val, triggerEvents);
    var control = self.getControl();

    if (control) {
      $(".ui-bizagi-render-label", control).text(val);
    }
  } });