/**
 *   Name: Bizagi Tablet Render Template Money Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the money render class inside templates to adjust to tablet devices
 */

bizagi.rendering.tablet.layoutRender.extend("bizagi.rendering.tablet.layoutMoney", bizagi.rendering.layoutNumber.prototype);
// Extends itself
bizagi.rendering.tablet.layoutMoney.extend("bizagi.rendering.tablet.layoutMoney", bizagi.rendering.layoutMoney.prototype);
bizagi.rendering.tablet.layoutMoney.extend("bizagi.rendering.tablet.layoutMoney", {
  /*
                                                                                     *   Sets the value in the rendered control
                                                                                     */
  setValue: function setValue(value, triggerEvents) {
    var self = this;
    value = value ? self.getDisplayValue(value) : self.getDisplayValue(0);
    self.properties.displayName = value;
    this._super(value, triggerEvents);
    var control = self.getControl();
    if (control) {
      $(".ui-bizagi-render-number", control).text(value);
    }
  } });